    const [state, setState] = useState([])
    const [pass, setPass] = useState("")



    useEffect(() => {
        fetch(`http://${ADRESS}:100/gettingusers`)
        .then(response => response.json())
        .then(json => setState(json))
    }, [])

    let passTable = createRef();


    function passFoo() {
        setPass(passTable.current.value)
    }



